<template>
<div class="class">
  <mq-layout mq="sm">
      <HelloWorld2 class="hello1"></HelloWorld2>

  <Flipbook class="flipbook-mobile"      
       :pages="this.pages1">  
      </Flipbook>
  </mq-layout>
  <mq-layout mq="md+">
      <HelloWorld class="hello1"></HelloWorld>
    <br>
    <Flipbook class="flipbook-desktop" v-slot="flipbook"   
       :pages="this.pages1">  
      <b-button id="hello" class="hello one" @click="count(); flipbook.flipLeft(); locateMe();">&laquo;</b-button>
      <b-button id="hello" class="hello two" @click="count(); flipbook.flipRight(); locateMe();">&raquo;</b-button>
  </Flipbook>
  </mq-layout>

</div>
</template>
<script>
// @ is an alias to /src
import axios from "axios";
import Flipbook from 'flipbook-vue'
import HelloWorld from '@/components/HelloWorld.vue'
import HelloWorld2 from '@/components/HelloWorld2.vue'


export default {
  name: 'Home',
  components: {
    Flipbook,
    HelloWorld,
    HelloWorld2
  },
  data() {
    return {
      pages: [
        "",
        "/yo.jpg",
        "/na.jpg",
        "/jo.jpg",
        "/ma.jpg",
      ],
      pages1:[],
      counting:0,
      ip_address : "",
      location:null,
    gettingLocation: false,
    errorStr:null,
    articleId: null

    }
  },
  methods: {
    postit() {
          var data = {   lat: this.location.coords.latitude,
          long: this.location.coords.longitude,
        count: this.counting }
        console.log(data);
       axios.post("https://newsletter.mpengs.nyc/get_ips", {data:data} ,{ params: data})
      .then(response => this.articleId = response.data.id);
    },
    count() {
      this.counting = this.counting + 1;
    },
async getLocation() {
      
      return new Promise((resolve, reject) => {

        if(!("geolocation" in navigator)) {
          reject(new Error('Geolocation is not available.'));
        }

        navigator.geolocation.getCurrentPosition(pos => {
          resolve(pos);
        }, err => {
          reject(err);
        });

      });
    },
    async locateMe() {

      this.gettingLocation = true;
      try {
        this.gettingLocation = false;
        this.location = await this.getLocation();
      } catch(e) {
        this.gettingLocation = false;
        this.errorStr = e.message;
      }
      this.postit();
    },
  },
    created() {
    let self = this;
    axios.get('https://newsletter.mpengs.nyc/get_event_pics3').then(function(response){    
   
    self.pages = response.data
    self.pages1 = self.pages.data
    });
    axios.get('https://api.ipify.org?format=json').then(function(response){    
    self.ip_address = response.data
    console.log(self.ip_address);
    });
}
}
</script>

